import React from 'react';
import { Col, Row } from 'antd';
import styled from 'styled-components';
import header from '@images/partners/partners-hero.svg';
import { device, size } from '@src/breakpoints';
import { emailLink } from '@components/Cta';
import {
  ContainerWrapper,
  HeroDescription,
  HeroHeader,
  MarketButton
} from '@src/Styles';

export const HeroArt = styled.div`
  text-align: right;

  img {
    width: 95%;
  }

  @media ${device.laptop} {
    img {
      width: 65%;
    }
  }

  @media ${device.laptopM} {
    img {
      width: 50%;
    }
  }
`;

export const TextWrapper = styled.div`
  padding: 50px 0;

  @media (max-width: ${size.tablet}) {
    padding: 25px 50px 50px 25px;
  }
`;

class Hero extends React.Component {
  render() {
    return (
      <section
        style={{
          background: '#181E26',
          minHeight: '300px'
        }}
      >
        <ContainerWrapper>
          <Row type="flex" className="hero" align="middle">
            <Col xs={24} sm={24} md={14} lg={12} xl={12} style={{ zIndex: 1 }}>
              <TextWrapper>
                <HeroHeader>
                  Build on top of <br />
                  MARKET Protocol
                </HeroHeader>
                <HeroDescription>
                  Teams all over the world are building applications and
                  exchanges on top of MARKET Protocol’s unique infrastructure.
                  We are looking for exceptional teams to help us build the
                  decentralized future! Join us.
                </HeroDescription>
                <MarketButton
                  type="primary"
                  size={'large'}
                  href={emailLink}
                  bg="#00FFE2"
                >
                  Become a Partner
                </MarketButton>
              </TextWrapper>
            </Col>
            <Col xs={0} sm={0} md={10} lg={12} xl={12}>
              <HeroArt>
                <img alt="MARKET partners" src={header} />
              </HeroArt>
            </Col>
          </Row>
        </ContainerWrapper>
      </section>
    );
  }
}
export default Hero;



