import React, { Component } from 'react';
import styled from 'styled-components';
import { Helmet } from 'react-helmet';
import { Col, Row } from 'antd';

import config, { PartnerProject } from './config';
import Hero from '@components/Partners/Hero';
import {
  ContainerWrapper,
  Description,
  MarketButton,
  Section,
  SectionHeader
} from '@src/Styles';
import { default as Cta, emailLink } from '@components/Cta';

const PartnerBlock = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch

  > a {
    display: flex;
    flex-direction: column;
    align-items: center
    justify-content: center;
    background-color: #fff;
    text-align: center;
    height: 100%;
    transition: all 150ms ease;
    
    :hover {
      box-shadow: 0 20px 25px 0 rgba(0,0,0,0.12);
    }
  }

  img {
    max-width: 100%;
  }
`;

const JoinCTAWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  background-color: #00e2c1;
  padding: 50px;
  margin-bottom: 50px;

  > div:last-child {
    text-align: center;
    margin-top: 30px;
  }
`;

class PartnersComponent extends Component {
  renderPartnerBlocks(partners: PartnerProject[]): null | JSX.Element[] {
    if (partners && partners.length) {
      return partners.map((p, i) => {
        return (
          <Col key={i} xs={24} sm={12} md={12} lg={8}>
            <PartnerBlock>
              <a href={p.url} target="_blank" className="partner-link">
                <img src={p.image} />
              </a>
            </PartnerBlock>
          </Col>
        );
      });
    } else {
      return null;
    }
  }

  render() {
    const { partnerProjects } = config;

    return (
      <div>
        <Helmet>
          <title>MARKET Protocol - Partner Projects</title>
          <meta name="keywords" content="MARKET Protocol Partners" />
          <meta
            name="description"
            content="MARKET Protocol - Partner Projects"
          />
        </Helmet>
        <Hero />
        <Section bg={'#f0f0f0'}>
          <ContainerWrapper>
            <SectionHeader color={'#000'}>Partner Projects</SectionHeader>
            <Row justify={'center'} align={'middle'}>
              {this.renderPartnerBlocks(partnerProjects)}
            </Row>
          </ContainerWrapper>
        </Section>
        <Section bg={'#f0f0f0'}>
          <ContainerWrapper style={{ background: '#00e2c1' }}>
            <JoinCTAWrapper>
              <div className="join-cta-banner">
                <SectionHeader color={'#000'}>Join our mission</SectionHeader>
                <Description color={'#000'}>
                  Whether you’re an existing decentralized exchange or a
                  traditional financial services group thinking about blockchain
                  technology, we’d love to hear from you. Drop us an email and
                  we will get back to you shortly.
                </Description>
              </div>
              <div>
                <MarketButton bg={'#000'} color={'#00FFE2'} href={emailLink}>
                  Become a Partner
                </MarketButton>
              </div>
            </JoinCTAWrapper>
          </ContainerWrapper>
        </Section>
        <Cta bg="#fff" showTeamButton />
      </div>
    );
  }
}

export default PartnersComponent;



